.popular-anime {
    margin-top: 2rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
    padding-left: 5rem;
    padding-right: 5rem;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 2rem;
    background-color: #fff;
    border-top: 5px solid #e5e7eb;
}

.popular-anime a {
    height: 500px;
    border-radius: 7px;
    border: 5px solid #e5e7eb;
}

.popular-anime a img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 5px;
}
  
  @media (max-width: 768px) {
    .PopularStyled .popular-anime {
      flex-direction: column; 
      padding-left: 2rem;
      padding-right: 2rem;
    }
  }
  